<template>
    <button class="quizBtn" :class="buttonStyle" :style="{ width: props.btnWidth, height: props.btnHeight, fontSize: props.btnFontSize, lineHeight: props.lineHeight}" @click="btnClick">
        <span class="quizBtn_span" v-for="(word, index) in props.btnText" :key="index" :style="getFontStyle(word)">{{ word }}</span>
    </button>
</template>

<script setup lang="ts">
import { ref, defineProps, defineEmits, defineExpose } from 'vue';
import { ButtonStatus } from '@/resources/web_enum';

const props = defineProps({
    btnWidth: String,
    btnHeight: String,
    btnText: String,
    btnFontSize: {
        type: String,
        default: '4.125vw'
    },
    btnStatus: {
        type: String,
        default: ButtonStatus.Normal
    },
    lineHeight: {
        type: String,
        default: '5vw'
    }
});

const emits = defineEmits(['quizBtnClick']);

const buttonStyle = ref({
    quizBtn_normal: props.btnStatus === ButtonStatus.Normal,
    quizBtn_Select: props.btnStatus === ButtonStatus.Select,
    quizBtn_unSelect: props.btnStatus === ButtonStatus.unSelect
});

const buttonStatus = ref(props.btnStatus);

function changeStatus(btnStatus) {
    console.log('JN - change status to ', btnStatus);
    buttonStatus.value = btnStatus;
    if (btnStatus == ButtonStatus.Normal) {
        buttonStyle.value = {
            quizBtn_normal: true,
            quizBtn_Select: false,
            quizBtn_unSelect: false
        };
    } else if (btnStatus == ButtonStatus.Select) {
        buttonStyle.value = {
            quizBtn_normal: false,
            quizBtn_Select: true,
            quizBtn_unSelect: false
        };
    } else {
        buttonStyle.value = {
            quizBtn_normal: false,
            quizBtn_Select: false,
            quizBtn_unSelect: true
        };
    }
}

function getFontStyle(word) {
    const isChinese = word.match(/[^\x00-\xff]/);
    return {fontFamily: isChinese ? 'Maison Neue' : 'Janson Text LT Std'}
}

function btnClick() {
    emits('quizBtnClick');
}

defineExpose({
    changeStatus
});

</script>

<style scoped>
.quizBtn {
    font-weight: 400;
    letter-spacing: 0.04em;
    text-align: center;
    font-size: 4.125vw;
    white-space: pre-line;
}


.quizBtn_normal {
    background-color: rgba(255, 255, 255, 0.4);
    color: #17274C;
    border-color: #17274C;
    border-width: 1px;
}

.quizBtn_Select {
    background-color: rgba(255, 255, 255, 0.8);
    color: #17274C;
    border-width: 0px;
    box-shadow: 4px 4px 4px 0px #00000040;
}

.quizBtn_unSelect {
    background-color: rgba(255, 255, 255, 0.4);
    color: rgba(23, 39, 76, 0.4);
    border-color: #17274C;
    border-width: 1px;
}

</style>