<template>
    <div class="customTable">
        <QuizButtonComponents 
        v-for="(item, index) in props.quizArray" 
        :ref="el => btnArray[index] = el" 
        :key="item.id"
        :btnWidth="props.btnWidth" 
        :btnHeight="props.btnHeight" 
        :btnFontSize="props.btnFontSize"
        :btnText="item.title" 
        :lineHeight="props.lineHeight" 
        @quizBtnClick="quizBtnClick(index)"
        ></QuizButtonComponents>
    </div>
</template>

<script setup lang="ts">
import { ref, defineProps, defineEmits, onMounted } from "vue";
import { ButtonStatus } from "@/resources/web_enum";
import QuizButtonComponents from "./QuizButtonComponents.vue";

const props = defineProps({
    quizArray: Array,
    btnWidth: String,
    btnHeight: String,
    btnFontSize: {
        type: String,
        default: '4.125vw'
    },
    preCurrentIndex: {
        type: Number,
        default: -1
    },
    lineHeight: {
        type: String,
        default: '5vw'
    }
});

const emits = defineEmits([
    'quizAnswerClick'
]);

const btnArray = ref([]);

function quizBtnClick(index) {
    for(var i = 0; i < btnArray.value.length; i++) {
        if (i == index) {
            btnArray.value[i].changeStatus(ButtonStatus.Select);
        } else {
            btnArray.value[i].changeStatus(ButtonStatus.unSelect);
        }
    }
    emits('quizAnswerClick', props.quizArray[index].id);
}

onMounted(() => {
    if (props.preCurrentIndex >= 0 ) {
        for(var i = 0; i < btnArray.value.length; i++) {
            if (i == props.preCurrentIndex) {
                btnArray.value[i].changeStatus(ButtonStatus.Select);
            } else {
                btnArray.value[i].changeStatus(ButtonStatus.unSelect);
            }
        }
    }
});

</script>

<style scoped>
.customTable {
    display: grid;
    place-items: center;
    gap: 1vh;
}

</style>