<template>
    <div class="cardCreate">
        <!-- <label class="cardCreate_title">選取祝福語</label> -->
        <div class="cardCreate_title_image_div">
            <img class="cardCreate_title_image" :src="ImagePath.image_title" />
        </div>
        <CustomImageCarouselComponents class="customImageCarousel" :imageArray="imageSourceArray" :imageCurrentIndex="imageIndex" @imageChange="imageIndexChange"></CustomImageCarouselComponents>
        <div class="cardTextAndButton">
            <div class="insideDiv">
                <div class="inputNameGroup">
                    <label class="inputNameTitle">To:</label>
                    <input class="shareNameInput" :style="{width: inputWidth + 'vw', fontSize: inputFontSize + 'vw'}" type="text" v-model="shareName" maxlength="14" @input="adjustInput" />
                </div>
                <CustomTableComponents class="shareTextGroup" :quizArray="shareTextArray" :btnWidth="'69.69vw'" :btnHeight="'9.38vw'" :btnFontSize="'2.81vw'" :preCurrentIndex="blessMessageIndex" :lineHeight="'3.75vw'" @quizAnswerClick="blessMessageCheck"></CustomTableComponents>
                <QuizButtonComponents class="shareBtn" ref="shareBtn" :btnWidth="'43.4375vw'" :btnHeight="'8.125vw'" :btnText="'查看我的客製化賀卡'" :btnFontSize="'3.44vw'" :btnStatus="shareBtnEnable" @quizBtnClick="shareBtnClick"></QuizButtonComponents>
            </div>
        </div>
    </div>
</template>

<script setup lang="ts">
import { ref, onBeforeMount, defineProps, defineEmits } from 'vue';
import { ImagePath } from '@/resources/web_image';
import { ButtonStatus } from '@/resources/web_enum';
import CustomTableComponents from '@/components/custom_ui/CustomTableComponents.vue';
import QuizButtonComponents from '@/components/custom_ui/QuizButtonComponents.vue';
import CustomImageCarouselComponents from '@/components/custom_ui/CustomImageCarouselComponents.vue';

const props = defineProps({
    cardImageIndex: Number, 
    cardName: String, 
    cardBlessMessageIndex: Number
});

const emits = defineEmits([
    'toCheckCard',
]);

const imageIndex = ref(props.cardImageIndex);
const shareBtn = ref();
const shareBtnEnable = ref((props.cardName != "" && props.cardBlessMessageIndex >= 0) ? ButtonStatus.Normal : ButtonStatus.unSelect);
console.log(`JN - props.cardName: ${props.cardName}`);
const shareName = ref(props.cardName);
const blessMessageIndex = ref(props.cardBlessMessageIndex);

const inputFontSize = ref(5.625);
const inputWidth = ref(42);

const maxWidth = 60;
const originWidth = 42;


const shareTextArray = [
    {
        id: 1,
        title: '冬日繁花展現傲然生命力，\n願您人生如花璀璨盛放！'
    },
    {
        id: 2,
        title: '雪地芳跡點亮這場冬日之旅，\n願您每一程皆浪漫隨心！'
    },
    {
        id: 3,
        title: '優美花姿譜出冬日頌歌，\n願您美願盎然成真！'
    }
]

const imageSourceArray = [
    ImagePath.card_1_bg, ImagePath.card_2_bg, ImagePath.card_3_bg, ImagePath.card_4_bg
]

function shareBtnClick() {
    if (shareBtnEnable.value === ButtonStatus.Normal) {
        emits('toCheckCard', imageIndex.value, shareName.value, blessMessageIndex.value)
    }
}

function adjustInput() {
    const textLength = calculateTextWidth(shareName.value);
    console.log(`JN - 文字長度: ${textLength}vw`);

    if (textLength > originWidth) {
        if (textLength > maxWidth) {
            inputWidth.value = maxWidth;
            const radio = maxWidth / textLength;
            inputFontSize.value = 5.625 * radio;
            console.log(`JN - 文字大小縮減比例: ${radio} , 變更後為: ${inputFontSize.value} vw`);
        } else {
            console.log(`JN - 輸入欄寬度: ${textLength} vw`);
            inputWidth.value = textLength;
            inputFontSize.value = 5.625;
        }
    }

    shareBtnOpen();
}

function calculateTextWidth(text) {
    var totalWidth = 0;
    for (const char of text) {
        if (char.match(/[^\x00-\xff]/)) {
            totalWidth += (5.625 * 1.3);
        } else {
            totalWidth += (5.625 / 1.3);
        }
    }
    console.log(`JN - 文字"${text}"基本寬度: ${totalWidth} , 現在文字大小為: ${inputFontSize.value} vw`);
    return totalWidth;
}

function blessMessageCheck(bless_id) {
    blessMessageIndex.value = bless_id - 1;
    shareBtnOpen();
}

function shareBtnOpen() {
    console.log(`JN - shareName: ${shareName.value}`)
    if (shareName.value != null) {
        if (shareName.value.length>0 && blessMessageIndex.value >= 0) {
            shareBtnEnable.value = ButtonStatus.Normal;
        } else {
            shareBtnEnable.value = ButtonStatus.unSelect;
        }
        shareBtn.value.changeStatus(shareBtnEnable.value);
    }
}

function imageIndexChange(index) {
    imageIndex.value = index;
}

</script>

