        top: '-9.82vh',
        width: '100vw',
        height: '100vh',
        quizTitle: 'Rose de Noël 系列',
        quizContent: 'Rose de Noël 系列中的寶石，​\n皆經過工匠嚴謹的挑選。\n您能辨認出這件作品的花瓣，​\n是採用什麼材質嗎？',
        testArray: [
            {
                id: 1,
                title: '珍珠母貝'
            },
            {
                id: 2,
                title: '蛋白石'
            },
            {
                id: 3,
                title: '玉髓'
            }
        ],
        answerId: 1,
        answer: '珍珠母貝'
