        quizContent: '請問世家是否為 高級珠寶系列 ​和 Fleurette 系列\n選用同樣高品質的璀璨鑽石？ ',
        testArray: [
            {
                id: 1,
                title: '是'
            },
            {
                id: 2,
                title: '不是'
            }
        ],
        answerId: 1,
        answer: '是'
    },
    {
        id: 3,
        quizImage: ImagePath.quiz_3,
        left: '0px',
        top: '0px',
        width: '100vw',
        height: '100vh',
        quizTitle: 'Frivole 系列',
        quizContent: '您知道 Frivole 7 朵花手鐲上\n鑲嵌了多少顆鑽石嗎？',
        testArray: [
            {
                id: 1,
                title: '大約100顆'
            },
            {
                id: 2,
                title: '大約200顆'
            },
            {
                id: 3,
                title: '大約300顆'
            }
        ],
        answerId: 3,
        answer: '大約300顆'
    },
    {
        id: 4,
        quizImage: ImagePath.quiz_4,
        left: '0px',
        top: '0px',
        width: '100vw',
        height: '100vh',
        quizTitle: 'Flowerlace 系列',
        quizContent: '結合蕾絲的透明感與花朵的姿態，Flowerlace\n高級珠寶系列融合了世家哪兩個主要靈感來源？',
        testArray: [
            {
                id: 1,
                title: '自然與高級時裝'
            },
            {
                id: 2,
                title: '宇宙與白雪'
            },
            {
                id: 3,
                title: '舞蹈與自然'
            }
        ],
        answerId: 1,
        answer: '自然與高級時裝'
    },
]

function getFontStyle(word) {
    const isChinese = word.match(/[^\x00-\xff]/);
    return {fontFamily: isChinese ? 'Maison Neue' : 'Janson Text LT Std'}
}

function quizAnswerSelect(index) {
    console.log('JN - quizAnswerSelect in quizView。', index);
    if(isSelect.value) {
        if (selectId.value == index) {
            if (selectId.value == quizDataArray[props.quizIndex].answerId) {
                console.log('JN - 你選對了');
                quizAnswerStatus.value = QuizAnswer.AnswerSuccess;
            } else {
                console.log('JN - 你選錯了');
                quizAnswerStatus.value = QuizAnswer.AnswerError;
            }
        } else {
            selectId.value = index;
        }
    } else {
        isSelect.value = !isSelect.value;
        selectId.value = index;
    }
}

function toNextQuizPage() {
    console.log('JN - toNextQuizPage');
    emits('toNextPage');
}


</script>

