<template>
    <div class="floatingButton">
        <div v-if="isShow(ButtonType.PrevBtn)">
            <button class="prevBtn" @click="prevBtnClick">
                <img class="prevIcon" :src="IconPath.backIcon" />
            </button>
        </div>
        <div v-if="isShow(ButtonType.MutedBtn)">
            <button class="mutedBtn" @click="mutedBtnClick">
                <img class="mutedIcon" :src="mutedIcon" />
            </button>
        </div>
    </div>
</template>

<script setup lang="ts">
import { ref, defineProps, defineEmits, onBeforeMount } from 'vue';
import { ButtonType } from '@/resources/web_enum';
import { IconPath } from '@/resources/web_image';

const props = defineProps({
    selectButtonType: ButtonType,
    isMuted: {
        type: Boolean,
        default: false
    }
})

const mutedIcon = ref( props.isMuted ? IconPath.unMuteIcon : IconPath.muteIcon);

const emits = defineEmits(['prevBtnClickOut', 'mutedBtnClickOut']);

const isMuted = ref(props.isMuted);

function isShow(buttonType) {
    return buttonType == props.selectButtonType;
}

function prevBtnClick() {
    console.log('prevBtn click');
    emits('prevBtnClickOut');
}

function mutedBtnClick() {
    isMuted.value = !isMuted.value;
    if (isMuted.value) {
        console.log('mutedBtn click now false change to true');
        mutedIcon.value = IconPath.unMuteIcon;
    } else {
        console.log('mutedBtn click now true change to false');
        mutedIcon.value = IconPath.muteIcon;
    }
    emits('mutedBtnClickOut', isMuted.value);
}

</script>

<style lang="css" scoped>

.prevBtn {
    width: 109px;
    height: 49px;
    border-radius: 24.5px;
    border: 0px;
    text-align: right;
    padding-right: 17.39px;
}

.prevIcon {
    width: 10.77px;
    height: 10.76px;
}

.mutedBtn {
    width: 48px;
    height: 48px;
    border-radius: 24px;
    border: 0px;
    background-color: transparent;
    text-align: center;
}

.mutedIcon {
    width: 48px;
    height: 48px;
}

</style>