<template>
    <div class="main_view">
        <!-- <MusicComponents ref="musicPlayer" class="musicPlayer" :musicSource="musicSource"></MusicComponents> -->
        <div class="index_2_div" v-if="pageStatus==PageStatus.FirstPage">
            <FirstViewElement class="firstViewElement" @firstViewStartBtn="firstViewStartBtn"></FirstViewElement>
        </div>
        <div class="index_2_div" v-if="pageStatus==PageStatus.Quiz_1Page">
            <QuizViewElement class="quizViewElement" :quizIndex="0" @toNextPage="quiz1Complete"></QuizViewElement>
        </div>
        <div class="index_2_div" v-if="pageStatus==PageStatus.Quiz_2Page">
            <QuizViewElement class="quizViewElement" :quizIndex="1" @toNextPage="quiz2Complete"></QuizViewElement>
        </div>
        <div class="index_2_div" v-if="pageStatus==PageStatus.Quiz_3Page">
            <QuizViewElement class="quizViewElement" :quizIndex="2" @toNextPage="quiz3Complete"></QuizViewElement>
        </div>
        <div class="index_2_div" v-if="pageStatus==PageStatus.Quiz_4Page">
            <QuizViewElement class="quizViewElement" :quizIndex="3" @toNextPage="quiz4Complete"></QuizViewElement>
        </div>
        <div class="index_2_div" v-if="pageStatus==PageStatus.EndPage">
            <EndViewElement class="endViewElement" @toCardPage="toCardCreate" @backToFirstPage="backToFirst"></EndViewElement>
        </div>
        <div class="index_2_div" v-if="pageStatus==PageStatus.CardCreatePage">
            <CardCreateElement class="cardCreateElement" :cardImageIndex="cardImageIndex" :cardName="cardName" :cardBlessMessageIndex="cardBlessMessageIndex" @toCheckCard="toCheckCardPage"></CardCreateElement>
        </div>
        <div class="index_2_div" v-if="pageStatus==PageStatus.CardReviewPage">
            <CardReviewElement class="cardReviewElement" :selectIndex="cardImageIndex" :userName="cardName" :blessIndex="cardBlessMessageIndex" @shareBtnClick="cardShareLine" @restartBtnClick="cardReviewRestart"></CardReviewElement>
        </div>
        <VideoComponents v-show="isVideoShow" class="video_style" ref="videoController" :videoSource="videoSource" @videoPlayEnd="videoPlayEnd"></VideoComponents>
        <FloatingButtonComponents v-if="isPrevBtnShow" :selectButtonType="ButtonType.PrevBtn" @prevBtnClickOut="prevBtnClick" class="main_prevBtn"></FloatingButtonComponents>
        <FloatingButtonComponents v-if="isMutedBtnShow" :selectButtonType="ButtonType.MutedBtn" :isMuted="isVideoMuted" @mutedBtnClickOut="mutedBtnClick" class="main_mutedBtn"></FloatingButtonComponents>
        <BlackMaskComponents v-if="blackMaskStatis==BlackMaskStauts.Mask01" class="blackMaskComponents" :showIndex="0" @clickBlackMask="clickBlackMaskArea"></BlackMaskComponents>
        <BlackMaskComponents v-if="blackMaskStatis==BlackMaskStauts.Mask02" class="blackMaskComponents" :showIndex="1" @clickBlackMask="clickBlackMaskArea"></BlackMaskComponents>
    </div>
</template>

<script setup lang="ts">
import { ref, onBeforeMount, onMounted } from 'vue';
import { ImagePath } from '@/resources/web_image';
import { VideoPath } from '@/resources/web_video';
import { MusicPath } from '@/resources/web_music';
import { ButtonType, ButtonStatus, BlackMaskStauts, PageStatus } from '@/resources/web_enum';
import FloatingButtonComponents from '@/components/custom_ui/FloatingButtonComponents.vue';
import VideoComponents from '@/components/custom_ui/VideoComponents.vue';
import FirstViewElement from '@/components/view_element/FirstViewElement.vue';
import QuizViewElement from '@/components/view_element/QuizViewElement.vue';
import MusicComponents from '@/components/custom_ui/MusicComponents.vue';
import EndViewElement from '@/components/view_element/EndViewElement.vue';
import CardCreateElement from '@/components/view_element/CardCreateElement.vue';
import CardReviewElement from '@/components/view_element/CardReviewElement.vue';
import BlackMaskComponents from '@/components/custom_ui/BlackMaskComponents.vue';
import liff from '@line/liff';

const isPrevBtnShow = ref(false);
const isMutedBtnShow = ref(true);
const blackMaskStatis = ref(BlackMaskStauts.Mask01);
const pageStatus = ref(PageStatus.FirstPage);
var prePageStatus = PageStatus.FirstPage;

const videoController = ref();
const videoSource = ref(VideoPath.first_video);
const isVideoShow = ref(true);
const isVideoMuted = ref(false);

const cardImageIndex = ref(0);
const cardName = ref('');
const cardBlessMessageIndex = ref(-1);

// const musicSource = ref(MusicPath.first_music);
// const musicPlayer = ref();

const profile = ref();

//首頁點擊
function firstViewStartBtn() {
    console.log('JN - to quiz01');
    videoController.value.videoPlayerSourceChange(VideoPath.quiz_1);
    // musicPlayer.value.musicPause();
    isVideoShow.value = true;
    isMutedBtnShow.value = true;
    videoController.value.videoPlayer();
    prePageStatus = PageStatus.Quiz_1Page;
}

function quiz1Complete() {
    console.log('JN - quiz1Complete');
    videoController.value.videoPlayerSourceChange(VideoPath.quiz_2);
    // musicPlayer.value.musicPause();
    isVideoShow.value = true;
    isMutedBtnShow.value = true;
    videoController.value.videoPlayer();
    prePageStatus = PageStatus.Quiz_2Page;
}

function quiz2Complete() {
    console.log('JN - quiz2Complete');
    videoController.value.videoPlayerSourceChange(VideoPath.quiz_3);
    // musicPlayer.value.musicPause();
    isVideoShow.value = true;
    isMutedBtnShow.value = true;
    videoController.value.videoPlayer();
    prePageStatus = PageStatus.Quiz_3Page;
}

function quiz3Complete() {
    console.log('JN - quiz3Complete');
    videoController.value.videoPlayerSourceChange(VideoPath.quiz_4);
    // musicPlayer.value.musicPause();
    isVideoShow.value = true;
    isMutedBtnShow.value = true;
    videoController.value.videoPlayer();
    prePageStatus = PageStatus.Quiz_4Page;
}

function quiz4Complete() {
    console.log('JN - quiz4Complete');
    prePageStatus = PageStatus.EndPage;
    pageStatus.value = PageStatus.EndPage;
}

function toCardCreate() {
    console.log('JN - toCardCreate');
    blackMaskStatis.value = BlackMaskStauts.Mask02;
    prePageStatus = PageStatus.CardCreatePage;
    pageStatus.value = PageStatus.CardCreatePage;
}

function backToFirst() {
    videoController.value.videoPlayerSourceChange(VideoPath.first_video);
    isVideoShow.value = true;
    isMutedBtnShow.value = true;
    isPrevBtnShow.value = false;
    // musicPlayer.value.musicPause();
    blackMaskStatis.value = BlackMaskStauts.Mask01;
    prePageStatus = PageStatus.FirstPage;
}

function toCheckCardPage(imageIndex, shareName, blessMessageIndex) {
    console.log(`JN - toCheckCardPage imageIndex: ${imageIndex}, shareName: ${shareName}, blessMessage`);
    cardImageIndex.value = imageIndex;
    cardName.value = shareName;
    cardBlessMessageIndex.value = blessMessageIndex;
    prePageStatus = PageStatus.CardReviewPage;
    pageStatus.value = PageStatus.CardReviewPage;
}

function cardReviewRestart() {
    cardImageIndex.value = 0;
    cardName.value = '';
    cardBlessMessageIndex.value = -1;
    prePageStatus = PageStatus.CardCreatePage;
    pageStatus.value = PageStatus.CardCreatePage;
}

function cardShareLine() {
    console.log('JN - share card to line friend');
    console.log('JN - check line profile', profile.value);

    if(!liff.isLoggedIn()) return;

    liff.shareTargetPicker([
        {
            type: 'text',
            text: `接收來自 ${profile.value.displayName} 與梵克雅寶給你的冬季專屬賀卡！邀請你一同體驗梵克雅寶的冬日秘境，尋找雪境中的動人珠寶！\n\n▼ 點擊前往冬日秘境`,
        },
        {
            type: 'image',
            originalContentUrl: 'https://pbs.twimg.com/media/E0ZdPT2VgAQOq6U.jpg',
            previewImageUrl: 'https://pbs.twimg.com/media/E0ZdPT2VgAQOq6U.jpg'
        },
        {
            type: 'flex',
            altText: '圖片分享訊息',
            contents: {
                type: 'bubble',
                hero: {
                    type: 'image',
                    url: 'https://i.ytimg.com/vi/H80Pky34xc4/maxresdefault.jpg',
                    size: 'full',
                    aspectRatio: '20:13',
                    aspectMode: 'cover',
                    action: {
                        type: 'uri',
                        uri: 'https://line.me/R/ti/p/@072chkdr',
                    }
                }
            },
        }
    ]).then(function (res) {
        if (res) {
            // console.log(`${res.status} message send`)
            console.log(`Line share message:${res.status}, and resultDescription: `, res)
        } else {
            console.log('Share target picker is closed!')
        }
    }).catch(function (error) {
        console.log(`share target picker send error: ${error.message}`)
    });
}

//影片截止時
function videoPlayEnd() {
    console.log("JN - MainView video play end");
    isVideoShow.value = false;
    isPrevBtnShow.value = true;
    isMutedBtnShow.value = false;
    // musicPlayer.value.musicPlay();
    if (prePageStatus == PageStatus.Quiz_1Page) {
        pageStatus.value = PageStatus.Quiz_1Page;
    } else if (prePageStatus == PageStatus.Quiz_2Page) {
        pageStatus.value = PageStatus.Quiz_2Page;
    } else if (prePageStatus == PageStatus.Quiz_3Page) {
        pageStatus.value = PageStatus.Quiz_3Page;
    } else if (prePageStatus == PageStatus.Quiz_4Page) {
        pageStatus.value = PageStatus.Quiz_4Page;
    } else if (prePageStatus == PageStatus.FirstPage) {
        pageStatus.value = PageStatus.FirstPage;
    }
}

//上一頁按鈕
function prevBtnClick() {
    console.log('JN - MainView prevBtn click');
    if (prePageStatus == PageStatus.FirstPage) {
        isVideoShow.value = true;
        isMutedBtnShow.value = true;
        isPrevBtnShow.value = false;
        // musicPlayer.value.musicPause();
        videoController.value.videoPlayer();
    } else if (prePageStatus == PageStatus.Quiz_1Page) {
        videoController.value.videoPlayerSourceChange(VideoPath.first_video);
        isVideoShow.value = true;
        isMutedBtnShow.value = true;
        isPrevBtnShow.value = false;
        // musicPlayer.value.musicPause();
        videoController.value.videoPlayer();
        prePageStatus = PageStatus.FirstPage;
    } else if (prePageStatus == PageStatus.Quiz_2Page) {
        videoController.value.videoPlayerSourceChange(VideoPath.quiz_1);
        isVideoShow.value = true;
        isMutedBtnShow.value = true;
        // musicPlayer.value.musicPause();
        videoController.value.videoPlayer();
        prePageStatus = PageStatus.Quiz_1Page;
    } else if (prePageStatus == PageStatus.Quiz_3Page) {
        videoController.value.videoPlayerSourceChange(VideoPath.quiz_2);
        isVideoShow.value = true;
        isMutedBtnShow.value = true;
        // musicPlayer.value.musicPause();
        videoController.value.videoPlayer();
        prePageStatus = PageStatus.Quiz_2Page;
    } else if (prePageStatus == PageStatus.Quiz_4Page) {
        videoController.value.videoPlayerSourceChange(VideoPath.quiz_3);
        isVideoShow.value = true;
        isMutedBtnShow.value = true;
        // musicPlayer.value.musicPause();
        videoController.value.videoPlayer();
        prePageStatus = PageStatus.Quiz_3Page;
    } else if (prePageStatus == PageStatus.EndPage) {
        videoController.value.videoPlayerSourceChange(VideoPath.quiz_4);
        isVideoShow.value = true;
        isMutedBtnShow.value = true;
        // musicPlayer.value.musicPause();
        videoController.value.videoPlayer();
        prePageStatus = PageStatus.Quiz_4Page;
    } else if (prePageStatus == PageStatus.CardCreatePage) {
        cardImageIndex.value = 0;
        cardName.value = '';
        cardBlessMessageIndex.value = -1;
        prePageStatus = PageStatus.EndPage;
        pageStatus.value = PageStatus.EndPage;
    } else if (prePageStatus == PageStatus.CardReviewPage) {
        prePageStatus = PageStatus.CardCreatePage;
        pageStatus.value = PageStatus.CardCreatePage;
    }
}

//靜音按鈕
function mutedBtnClick(isMuted: boolean) {
    if (isMuted) {
        console.log('JN - MainView mutedBtn click, 現在靜音中');
    } else {
        console.log('JN - MainView mutedBtn click, 現在有聲音中');
    }
    isVideoMuted.value = isMuted;
    videoController.value.videoPlayerMuted(isMuted);
    // musicPlayer.value.changeMutedStatus(isMuted);

}

//黑色遮罩點擊
function clickBlackMaskArea() {
    console.log('JN - clickBlackMaskArea');
    if (blackMaskStatis.value == BlackMaskStauts.Mask01) {
        blackMaskStatis.value = BlackMaskStauts.None;
        videoController.value.videoPlayer();
    } else if (blackMaskStatis.value == BlackMaskStauts.Mask02) {
        blackMaskStatis.value = BlackMaskStauts.None;
    }
}

const initializeLiff = () => {
    try {
        liff.init({liffId: '1551453274-ANYeG98Q'}).then(async() => {
            if(liff.isLoggedIn()) {
                liff.getProfile().then((profile) => {
                    console.log('JN - line is login:', profile)
                    liff.getAccessToken();
                })
                profile.value = await liff.getProfile()
            } else {
                console.log('JN - line not login');
                liff.login();
            }
        });
    } catch(error) {
        console.log('JN - liff init error:', error)
    }
}

onMounted(() => {
    initializeLiff();
})

</script>

