<template>
    <video ref="video_components" class="video_style">
        <source :src="videoSourcePath" type="video/mp4">
    </video>
</template>

<script setup lang="ts">
import { ref, defineProps, defineEmits, defineExpose, onMounted } from 'vue';
import { VideoPath } from '@/resources/web_video'

const props = defineProps({
    videoSource: String,
});

const emits = defineEmits(['videoPlayEnd']);

const video_components = ref();
const videoSourcePath = ref(props.videoSource)

onMounted(() => {
    video_components.value.addEventListener('ended', () => {
        console.log('JN - in components play end');
        emits('videoPlayEnd');
    });
});

function videoPlayer() {
    console.log('JN - now play: ', videoSourcePath.value);
    video_components.value.play();
}

function videoPlayerSourceChange(source) {
    videoSourcePath.value = source;
    video_components.value.load();
}

function videoPlayerMuted(isMuted) {
    video_components.value.muted = isMuted;
}

defineExpose({
    videoPlayer, videoPlayerSourceChange, videoPlayerMuted
});
</script>

<style scoped>
.video_style {
    width: auto;
    height: 100vh;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
}
</style>