<template>
    <div class="CustomImageCarousel" 
    @mousedown="startDrag" @mousemove="onDrag" @mouseup="endDrag" 
    @touchstart="startDrag" @touchmove="onDrag" @touchend="endDrag">
        <div class="carousel">
            <img class="carouselImage" :src="props.imageArray[currentIndex]" />
            <div class="imageCover"></div>
            <div class="imageMoveBtnGroup">
                <div class="imageMoveBtnGroupInside">
                    <button class="prevSlideBtn" @click="prevSlide">
                        <img class="SlideBtn" :src="IconPath.prevBtn2Icon" />
                    </button>
                    <button class="nextSlideBtn" @click="nextSlide">
                        <img class="SlideBtn" :src="IconPath.nextBtn2Icon" />
                    </button>
                </div>
            </div>
        </div>
    </div>
</template>

<script setup lang="ts">
import { ref, defineProps, defineEmits, computed, onMounted } from 'vue';
import { IconPath } from '@/resources/web_image';

const props = defineProps({
    imageArray: Array,
    imageCurrentIndex: {
        type: Number,
        default: 0
    }
});

const emits = defineEmits([
    'imageChange'
]);

const currentIndex = ref(props.imageCurrentIndex);
const dragging = ref(false);
const startX = ref(0);
const offsetX = ref(0);

function startDrag(e) {
    dragging.value = true;
    startX.value = e.type === 'mousedown' ? e.clientX : e.touches[0].clientX;
}

function endDrag(e) {
    dragging.value = false;
    if (offsetX.value > 100) prevSlide();
    else if (offsetX.value < -100) nextSlide();
    offsetX.value = 0;
}

function onDrag(e) {
    if(!dragging.value) return;
    const currentX = e.type === 'mousemove' ? e.clientX : e.touches[0].clientX;
    offsetX.value = currentX - startX.value;
}

function prevSlide() {
    currentIndex.value = (currentIndex.value - 1 + props.imageArray.length) % props.imageArray.length;
    currentIndexOutput();
}

function nextSlide() {
    currentIndex.value = (currentIndex.value + 1) % props.imageArray.length;
    currentIndexOutput();
}

function currentIndexOutput() {
    emits('imageChange', currentIndex.value)
}

</script>

<style scoped>
