    position: absolute;
    width: 100vw;
    height: 100vh;
    top: 0px;
    left: 0px;
    right: 0px;
    bottom: 0px;
    z-index: 2;
}

.carouselImage {
    position: absolute;
    top: 0px;
    left: 0px;
    width: 100vw;
    height: 100vh;
    object-fit: fill;
    z-index: 1;
}

.imageMoveBtnGroup {
    position: absolute;
    z-index: 3;
    width: 100vw;
}

.imageMoveBtnGroupInside {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 4.69vw;
}

.prevSlideBtn {
    background-color: transparent;
    border: none;
}

.nextSlideBtn {
    background-color: transparent;
    border: none;
}

.SlideBtn {
    width: 2.47vw;
    height: 5vw;
    filter: drop-shadow(4px 4px 8px #4B7DA1FF); /* 4B7DA180會看不出來，先暫時調整為非透明 */
}

.prevIconContainer{
    display: flex; /* 使圖示在同一行顯示 */
    align-items: center; /* 垂直置中 */
