    width: 15.8px; /* 設置容器寬度 */
    height: 32px; /* 設置容器高度 */
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.5); /* 設置陰影 */
    background-color: transparent; /* 背景透明 */
